body{
  background-color:#EEEEEE;
}
.todolist{
  background-color:#FFF;
  padding:20px 20px 10px 20px;
  margin-top:30px;
}
.todolist h1{
  margin:0;
  padding-bottom:20px;
  text-align:center;
}
.form-control{
  border-radius:0;
}
li.ui-state-default{
  background:#fff;
  border:none;
  border-bottom:1px solid #ddd;
}

li.ui-state-default:last-child{
  border-bottom:none;
}

.todo-footer{
  background-color:#F4FCE8;
  margin:0 -20px -10px -20px;
  padding: 10px 20px;
}
ul > .done-items {
  padding:10px 0;
  border-bottom:1px solid #ddd;
  text-decoration:line-through;
  color: gray;
}
#done-items li:last-child{
  border-bottom:none;
}
#checkAll{
  margin-top:10px;
}
li > button{
  float: right ;
}
li > button:hover{
  color: red;
  cursor: pointer;
}
.remove-item{
  border: 0;
  background: transparent;
}
.checkbox{
  display: inline;
}